import { Button } from '@mui/material'
import React from 'react'
import Compare from 'react-compare-image'
import pictures from '../media/comparison.json'
export function ComparisonPage () {
    const [con, setCon] = React.useState(false)
    const confirm = () => {
        setCon(true)
    }
    const cancel = () => {
        // eslint-disable-next-line no-restricted-globals
        history.back()
    }
    const mod = () => {
        window.location.href = "https://gamebanana.com/mods/418207"
    }
    if (!con) return (
        <div style={{textAlign: 'center'}}>
            <br/>
            <h1>This page contains NSFW content.</h1>
            <p>Kamoshida's palace is one of the main reasons for Persona 5's high age rating.</p>
            <p> If you are under 18 or don't want to see this, please hit the cancel button or visit the mod page.</p>
            <br/>
            <Button style={{ marginLeft: '25px', marginRight: '25px' }} onClick={cancel}>Back to Safety</Button>
            <Button style={{ marginLeft: '25px', marginRight: '25px' }} onClick={mod}>Mod Page</Button>
            <Button style={{ marginLeft: '25px', marginRight: '25px' }} onClick={confirm}>Continue</Button>
        </div>
    )
    else 
    return (
        <div>
            <h1 style={{fontSize: "50px", textAlign: "center"}}>Kamoshida's Palace</h1>
            {
                pictures.map(p => {
                    return (
                        <div style={{width: '95vw', marginLeft: "auto", marginRight: 'auto'}}>
                            <br/>
                            {
                                p.comment ? <h2>{p.comment}</h2> : <></>
                            }
                            <Compare leftImageLabel='Original' leftImage={p.original} rightImageLabel="Modified" rightImage={p.modified} sliderPositionPercentage={(p.percent ?? 50) / 100}/>
                            <br/>
                        </div>
                        
                    )
                })
            }
        </div>
    )
}